import React, { useEffect, useRef } from "react";
import "./Intro.scss";

const useSvgTop = (top = 0, delay = 1, duration = 1) => {
  const element = useRef();
  useEffect(() => {
    if (element.current) {
      const { current } = element;
      current.style.animationDelay = `${delay}s`;
      current.style.animationDuration = `${duration}s`;
      setTimeout(() => {
        current.style.top = `${top}%`;
        setTimeout(() => {
          current.classList.add("float");
          current.style.animationDelay = `0s`;
          current.style.animationDuration = `3s`;
        }, duration * 1000);
      }, delay * 1000);
    }
  }, [top, delay, duration]);

  if (typeof top !== "number" || typeof delay !== "number") {
    return;
  }
  return { ref: element };
};

const useClickable = (delay = 1) => {
  const element = useRef();
  useEffect(() => {
    if (element.current) {
      setTimeout(() => {
        element.current.addEventListener("click", () => {
          let cover = element.current.getElementsByClassName("intro__cover")[0];
          cover.classList.add("start");
          cover.style.left = `0`;
          setTimeout(() => (window.location.href = "/Home"), 1000);
        });
      }, delay * 1000);
    }
  });

  if (typeof delay !== "number") {
    return;
  }

  return { ref: element };
};

